.bottomNavMainContainer {
    width: 100vw;
    height: 10vh;
    position: fixed;
    bottom: 0;
}

.grid-container {
    display: grid;
    grid-template-columns: auto auto auto;
    background-color: blanchedalmond;
  }
  .grid-item {
    background-color: blanchedalmond;
    border: 1px solid rgba(0, 0, 0, 0.8);
    padding: 20px;
    font-size: 30px;
    text-align: center;
    color: black;
    text-decoration: none;
  }

  .grid-item:hover {
    background-color: rgb(207, 195, 160);
  }
  